/* typography
*************/

@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");

p,
.p {
    margin-bottom: 1.25em;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5 {
    margin: 2.75rem 0 1rem;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    line-height: 1.15;
}

h1,
.h1 {
    margin-top: 0;
    font-size: 4.209em;
}

h2,
.h2 {
    font-size: 3.157em;
}

h3,
.h3 {
    font-size: 2.369em;
}

h4,
.h4 {
    font-size: 1.777em;
}

h5,
.h5 {
    font-size: 1.333em;
}

small,
.text_small {
    font-size: 0.75em;
}
